import Lofte from "./components/Lofte.vue";
import LofteTours from "./components/LofteTours.vue";
import LofteLocations from "./components/LofteLocations.vue";
import LofteBookings from "./components/LofteBookings.vue";
import LofteContact from "./components/LofteContact.vue";

export const routes = [
    {
        path: "/",
        component: Lofte
    },
    {
        path: "/tours",
        component: LofteTours
    },
    {
        path: "/locations",
        component: LofteLocations
    },
    {
        path: "/bookings",
        component: LofteBookings
    },
    {
        path: "/contact",
        component: LofteContact
    },
];



