<template>
  <div class="row d-flex text-center align-items-center pl-4 pr-4 pt-4 services text-white">
    <div class="col-lg-4 mb-5">
      <i class="icon fas fa-map-marker-alt"></i>
      <h3>Tour Packages</h3>
      <p>We offer tour packages to fit all needs and budgets</p>
      <button @click="$router.push('/tours')" class="btn btn-primary">Learn more</button>
    </div>
    <div class="col-lg-4 mb-5">
      <i class="icon fas fa-map-marked-alt"></i>
      <h3>Loctions</h3>
      <p>We offer tours in a wide variety of locations throughout Denmark.</p>
      <button @click="$router.push('/locations')" class="btn btn-primary">Learn more</button>
    </div>
    <div class="col-lg-4 mb-5">
      <i class="icon fas fa-bookmark"></i>
      <h3>Book Now</h3>
      <p>Book your next unforgettable journey now.</p>
      <button @click="$router.push('/bookings')" class="btn btn-primary">Book now</button>
    </div>
  </div>
</template>

<script>
export default {};
</script>

<style scoped>
.btn-primary,
.btn {
  background: none !important;
  outline: none;
  border: 1px solid white;
  color: white;
}
.btn-primary:hover,
.btn:hover {
  background: white !important;
  outline: none;
  border: 1px solid white;
  box-shadow: none;
  color: #c1272d;
}
.btn-primary:active,
.btn:active {
  background: #851c20 !important;
  outline: none;
  border: none;
  box-shadow: none;
}

.btn-primary:focus,
.btn:focus {
  background: #851c20 !important;
  outline: none;
  border: none;
  box-shadow: none !important;
}

.icon {
  font-size: 60px;
  margin-bottom: 15px;
}

.services {
  background: #c1272d;
}

h3 {
  font-weight: 700;
}
</style>



