<template>
  <div style="z-index: 99 !important">
    <div @click="closeMenu" class="background-overlay"></div>
    <div class="row d-flex justify-content-center">
      <div class="col-lg-6 menu">
        <div class="row">
          <div class="col text-center">
            <img
              style="cursor: pointer;"
              @click="$router.push('/portfolio/lofte/')"
              src="../../public/Lofte_logo_red.png"
              alt
            >
          </div>
        </div>
        <div class="row mt-5 pb-4 text-center">
          <div class="divider">
            <img src="../../public/divider.png" alt>
          </div>
          <div @click="$router.push('/tours')" class="col menu-item">
            <i class="icon fas fa-map-marker-alt"></i>
            <h5>Tours</h5>
          </div>
          <div @click="$router.push('/locations')" class="col menu-item">
            <i class="icon fas fa-map-marked-alt"></i>
            <h5>Locations</h5>
          </div>
        </div>
        <div class="row mt-4 text-center">
          <div @click="$router.push('/bookings')" class="col menu-item">
            <i class="icon fas fa-calendar-alt"></i>
            <h5>Bookings</h5>
          </div>
          <div @click="$router.push('/contact')" class="col menu-item">
            <i class="icon fas fa-envelope"></i>
            <h5>Contact</h5>
          </div>
        </div>
        <div class="row mt-4 text-center">
          <div class="col">
            <p>
              <span class="lang-active">English</span> |
              <span class="lang">Danske</span>
            </p>
          </div>
          <div class="close-button">
            <i @click="closeMenu" class="far fa-times-circle"></i>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    menuCheck: {
      type: Boolean,
      required: true
    }
  },
  methods: {
    closeMenu() {
      this.menuCheck = !this.menuCheck;
      return this.$emit("closeMenu", this.menuCheck);
    }
  }
};
</script>

<style scoped>
.menu {
  position: fixed;
  height: 500px;
  background: white;
  border-radius: 5px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.432);
  padding: 20px;
  z-index: 98 !important;
}

.menu-item {
  color: gray;
  transition: all 0.2s;
  cursor: pointer;
}

.menu-item:hover {
  color: #c1272d;
  transition: all 0.2s;
}

.background-overlay {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.582);
  z-index: 97 !important;
}

.icon {
  font-size: 60px;
  margin-bottom: 15px;
}

.border-center {
  border-bottom: 1px solid #c1272d;
}

.border-middle {
  border-left: 1px solid #c1272d;
}

.divider {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -42%);
}

.lang {
  color: gray;
  transition: all 0.2s;
  cursor: pointer;
}

.lang:hover {
  color: #c1272d;
  transition: all 0.2s;
}

.lang-active {
  color: #c1272d;
  font-weight: bold;
  cursor: pointer;
}

.close-button {
  position: absolute;

  font-size: 30px;
  color: gray;
  transition: all 0.2s;
  cursor: pointer;
  bottom: 10px;
  right: 20px;
}

.close-button:hover {
  color: #c1272d;
  transition: all 0.2s;
}

.show-menu-enter-active,
.show-menu-leave-active {
  transition: opacity 0.5s;
  z-index: 99 !important;
}

.show-menu-enter,
.show-menu-leave-to {
  opacity: 0;
  z-index: 99 !important;
}
</style>



