<template>
  <nav>
    <div class="row menu-bar d-flex align-items-center">
      <div class="col brand">
        <img @click="$router.push('/')" src="../../public/Lofte_logo.png" alt>
      </div>
      <div class="col text-right">
        <i @click="toggleMenu" class="menu-selector fas fa-th-large"></i>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  data() {
    return {};
  },
  props: {
    menuCheck: {
      type: Boolean,
      required: true
    }
  },
  methods: {
    toggleMenu() {
      this.menuCheck = !this.menuCheck;
      return this.$emit("openMenu", this.menuCheck);
    }
  }
};
</script>

<style scoped>
.menu-bar {
  background: #c1272d;
  height: 100%;
  color: white;
  padding: 5px 0px;
}

.brand > img {
  height: 30px;
  cursor: pointer;
}

.menu-selector {
  font-size: 30px;
  transition: all 0.2s;
  cursor: pointer;
}

.menu-selector:hover {
  color: #8a1c1f;
  transition: all 0.2s;
}
</style>



