<template>
  <div data-aos="fade-down">
    <LofteHeader :menuCheck="menuStatus" @openMenu="menuStatus = $event"/>
    <LofteMenu v-if="menuStatus" :menuCheck="menuStatus" @closeMenu="menuStatus = $event"/>
    <LofteMain/>
    <More/>
    <LofteFooter/>
  </div>
</template>

<script>
import LofteHeader from "./LofteHeader";
import LofteMain from "./LofteMain";
import LofteFooter from "./LofteFooter";
import LofteMenu from "./LofteMenu";
import More from "./More";

export default {
  components: {
    LofteHeader,
    LofteMain,
    LofteFooter,
    LofteMenu,
    More
  },
  data() {
    return {
      menuStatus: false
    };
  }
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");

* {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
}
</style>



