<template>
  <div class="row info d-flex justify-content-center align-items-center text-white">
    <div class="col-lg-6 mt-4 mb-4 text-center">
      <img class="logo" src="../../public/lofte-logo-2.png" alt>
    </div>
    <div class="col-lg-6 pb-4 text-lg-left text-center feature-text">
      <h1>Escape Normal</h1>
      <p>
        <strong>Book your next unforgettable adventure today</strong>
      </p>
      <button @click="$router.push('/bookings')" class="btn btn-primary btn-lg">Book now</button>
    </div>
  </div>
</template>

<script>
export default {};
</script>

<style scoped>
.info {
  background: url("../../public/feature_background_1.jpg");
  background-position: center;
  background-size: cover;
}

@keyframes float {
  0% {
    transform: translateY(-10px);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}

.logo {
  height: 300px;
  animation-name: float;
  animation-duration: 4s;
  animation-iteration-count: infinite;
}

h1 {
  font-weight: 700;
}

.btn-primary,
.btn {
  background: none !important;
  outline: none;
  border: none;
  color: white;
  background-color: #c1272d !important;
}
.btn-primary:hover,
.btn:hover {
  background: white !important;
  outline: none;
  border: none;
  box-shadow: none;
  color: #c1272d;
}
.btn-primary:active,
.btn:active {
  background: #851c20 !important;
  outline: none;
  border: none;
  box-shadow: none;
}

.btn-primary:focus,
.btn:focus {
  background: #851c20 !important;
  outline: none;
  border: none;
  box-shadow: none !important;
}
</style>



