<template>
  <div class="row p-4 text-center">
    <div class="col-lg-12">
      <h3>
        <strong>Ready to book your next unforgettable adventure?</strong>
      </h3>
      <p>Book with us or contact us today and we'll make it happen!</p>
      <button
        @click="$router.push('/portfolio/lofte/bookings')"
        class="btn btn-primary mr-2"
      >Book Now</button>
      <button @click="$router.push('/portfolio/lofte/contact')" class="btn btn-primary">Contact Us</button>
    </div>
  </div>
</template>

<script>
export default {};
</script>

<style scoped>
.btn-primary,
.btn {
  background: none !important;
  outline: none;
  border: none;
  color: #c1272d;
  border: 1px solid #c1272d !important;
}
.btn-primary:hover,
.btn:hover {
  background: #c1272d !important;
  outline: none;
  border: none;
  box-shadow: none;
  color: white;
}
.btn-primary:active,
.btn:active {
  background: #851c20 !important;
  outline: none;
  border: none;
  box-shadow: none;
}

.btn-primary:focus,
.btn:focus {
  background: #851c20 !important;
  outline: none;
  border: none;
  box-shadow: none !important;
}
</style>



