<template>
  <div data-aos="fade-down">
    <LofteHeader :menuCheck="menuStatus" @openMenu="menuStatus = $event"/>
    <LofteMenu v-if="menuStatus" :menuCheck="menuStatus" @closeMenu="menuStatus = $event"/>
    <div class="row tours text-center text-white">
      <div class="col-lg-12">
        <h1 style="font-weight: bold">Locations</h1>
        <p>Here are just some of the many locations that we will make stops on in your journey.</p>
        <button @click="$router.push('/bookings')" class="btn btn-primary">Book Now!</button>
      </div>
    </div>
    <div class="row text-center mt-4 mb-4">
      <input
        class="form-control text-center"
        type="text"
        placeholder="Search locations"
        @keyup.prevent="show()"
        v-model="searchLocation"
      >
    </div>
    <div v-if="searchLocation == ''" class="row text-center">
      <table class="table table-hover">
        <thead style="background: #c1272d" class="text-white">
          <tr>
            <th scope="col">Location</th>
            <th scope="col">The Jaunt</th>
            <th scope="col">The Excursion</th>
            <th scope="col">The Grand Adventure</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(location) in locations" :key="location">
            <td>{{location}}</td>
            <td>{{getHas()}}</td>
            <td>{{getHas()}}</td>
            <td>{{getHas()}}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <div v-else class="row text-center">
      <table class="table table-hover">
        <thead style="background: #c1272d" class="text-white">
          <tr>
            <th scope="col">Location</th>
            <th scope="col">The Jaunt</th>
            <th scope="col">The Excursion</th>
            <th scope="col">The Grand Adventure</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(location) in show()" :key="location">
            <td>{{location}}</td>
            <td>{{getHas()}}</td>
            <td>{{getHas()}}</td>
            <td>{{getHas()}}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <LofteFooter/>
  </div>
</template>

<script>
import LofteHeader from "./LofteHeader";
import LofteFooter from "./LofteFooter";
import LofteMenu from "./LofteMenu";

export default {
  components: {
    LofteHeader,
    LofteFooter,
    LofteMenu
  },
  data() {
    return {
      searchLocation: "",
      menuStatus: false,
      locations: [
        "Copenhagen",
        "Aarhus",
        "Odense",
        "Aalborg",
        "Frederiksberg",
        "Esbjerg",
        "Gentofte",
        "Gladsaxe",
        "Randers",
        "Kolding",
        "Horsens",
        "Lyngby-Taarbæk",
        "Vejle",
        "Hvidovre",
        "Roskilde",
        "Helsingør",
        "Herning",
        "Silkeborg",
        "Næstved",
        "Greve Strand",
        "Tårnbya",
        "Fredericia",
        "Ballerup",
        "Rødovre",
        "Viborg",
        "Køge",
        "Holstebro",
        "Brøndbya",
        "Taastrupa",
        "Slagelse",
        "Hillerød",
        "Albertslunda",
        "Sønderborg",
        "Svendborg",
        "Herleva",
        "Holbæk",
        "Hjørring",
        "Hørsholm",
        "Frederikshavn",
        "Glostrupa",
        "Haderslev",
        "Nørresundbyb",
        "Ringsted",
        "Ølstykke-Stenløse",
        "Skive"
      ]
    };
  },
  methods: {
    getHas() {
      let yes = Math.floor(Math.random() * 10);
      if (yes > 5) {
        return "yes";
      } else {
        return "--";
      }
    },
    show() {
      return this.locations.filter(location =>
        location.toLowerCase().includes(this.searchLocation.toLowerCase())
      );
    }
  }
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");

* {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
}

.tours {
  background: linear-gradient(
    180deg,
    rgb(20, 78, 165) 0%,
    rgb(49, 126, 214) 100%
  );
  padding: 20px;
  background-size: cover;
  color: black;
}

.card-title {
  font-weight: bold;
}

.btn-primary,
.btn {
  background: #c1272d !important;
  outline: none;
  border: 1px solid #c1272d !important;
  box-shadow: none;
  color: white;
}
.btn-primary:hover,
.btn:hover {
  background: white !important;
  outline: none;
  border: none;
  color: #c1272d;
  border: 1px solid white !important;
}
.btn-primary:active,
.btn:active {
  background: #851c20 !important;
  outline: none;
  border: none;
  box-shadow: none;
}

.btn-primary:focus,
.btn:focus {
  background: #851c20 !important;
  outline: none;
  border: none;
  box-shadow: none !important;
}

input:active {
  border: 1px solid #c1272d;
}
input:focus {
  border: 1px solid #c1272d;
  box-shadow: none;
}
</style>



